/*******************************************************************************
 * Header
 ******************************************************************************/

header {
  margin-bottom: 20px;
}

#title {
  flex: 1;
}

#version {
  padding: 0.3em 0.5em;
  border-radius: var(--border-radius);
  font-weight: 600;
  color: rgb(89, 164, 6);
  background-color: rgba(89, 164, 6, 0.25);
}

h1 {
  margin: 10px 0 14px;
}

#title p {
  margin: 0;
}

/*******************************************************************************
 * Updates
 ******************************************************************************/

#improvements h2::before {
  background-image: url(/skin/icons/improvements.svg);
}

#fixes h2::before {
  background-image: url(/skin/icons/fixes.svg);
}

.updates {
  padding: 0;
  line-height: 22px;
}

.updates > li {
  padding: 20px 50px;
  margin-bottom: 20px;
  border: 1px solid #ebebeb;
  border-radius: var(--border-radius);
  background-color: #fff;
  list-style: none;
}

html[dir="ltr"] .updates > li {
  padding-right: 100px;
}

html[dir="rtl"] .updates > li {
  padding-left: 100px;
}

.updates h3 {
  position: relative;
  margin-top: 0;
  font-size: 1rem;
  color: #5a5a5a;
}

.updates h3::before {
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url(/skin/icons/update.svg);
  background-size: 100%;
  content: "";
}

html[dir="ltr"] .updates h3::before {
  left: -35px;
}

html[dir="rtl"] .updates h3::before {
  right: -35px;
}

.updates p {
  font-size: 0.9em;
  color: #757575;
}

.updates p:last-child {
  margin-bottom: 0;
}

.updates a {
  font-size: 1rem;
  font-weight: 600;
  color: var(--color-link);
  text-decoration: none;
}

.updates a:hover {
  text-decoration: underline;
}

.updates img,
.updates video {
  border: 1px solid #dcdcdc;
}

.updates video,
.updates img,
.updates .fallback {
  position: relative;
  max-width: 500px;
  width: 100%;
  background-color: #f8f8f8;
}

.updates .fallback {
  text-align: center;
}

/*******************************************************************************
 * Contribute
 ******************************************************************************/

#contribute p {
  color: #5a5a5a;
}

#contribute h3 {
  margin: 0;
  line-height: 45px;
  font-size: 1.5em;
}

#contribute > header {
  text-align: center;
}

#contribute > .columns {
  display: flex;
}

#contribute > .columns > section {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: flex-start;
  max-width: 460px;
  margin: auto;
  padding: 20px 60px 20px 24px;
  border: 1px solid #ebebeb;
  border-radius: var(--border-radius);
  background-color: #fff;
}

html[dir="ltr"] #contribute > .columns > section:not(:last-child) {
  margin-right: 20px;
}

html[dir="rtl"] #contribute > .columns > section:not(:last-child) {
  margin-right: 20px;
}

#contribute .block-icon {
  --icon-width: 38px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100%;
}

#contribute .block-icon::before {
  top: 0.25em;
}

#contribute .block-icon p {
  flex: 1;
  margin-top: 0.75em;
  margin-bottom: 1.5em;
}

body[data-store="edge"] #contribute-rate {
  display: none;
}

#contribute-rate > .block-icon::before {
  background-image: url(/skin/icons/waving.svg);
}

#contribute-donate > .block-icon::before {
  background-image: url(/skin/icons/donate.svg);
}
